.overlay {
    height: 230px;
    // width: 320px;
    margin-left: auto;
    margin-right: auto;
    background-image: linear-gradient(
          to right,
         rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56),
        ),
        url('../images/img-header/home1x-mob.png');
    // background-image: url('../images/img-header/home1x-mob.png');
    background-repeat: no-repeat;
    background-size: cover;


    @media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .overlay {
    background-image: linear-gradient(
          to right,
         rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56),
        ),
        url('../images/img-header/home2x-mob.png');
    // background-image: url('../images/img-header/home2x-mob.png');
  }
}

@media screen and (min-device-pixel-ratio: 3),
  screen and (min-resolution: 288dpi),
  screen and (min-resolution: 3dppx) {
  .overlay {
     background-image: linear-gradient(
