.container-header {
    padding-top: 24px;
    padding-bottom: 98px;

    @media screen and (min-width: 768px) {
       padding-top: 20px;
    }
}

.main-nav {
    display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 23px;
  height: 64px;

  @media screen and (min-width: 768px) {
      height: 75px;
      margin-bottom: 18px;
  }
}

.logo-link {
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 20px 0;
}

.logo-header {
    display: none;
    
    @media screen and (min-width: 768px) {
        display: inline-flex;
        align-items: center;
        margin-left: 10px;
        color: $white-color;
        font-weight: 500;
        font-size: 30px;
        line-height: 1.17;
  }
}

.navigation {
    display: flex;
    margin-left: auto;
}

.nav-list {
    @extend %list;  
    display: flex;
    align-items: center;
    min-height: 75px;
}
    
.nav-item:not(:nth-child(1)) {
     margin-left: 40px;
}

.nav-link {
    display: block;
    padding: 20px 0;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.17;
    color: $white-color;

    &-current {
      position: relative;
    }
}

.nav-link-current::after {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    border-radius: 2px;
    // box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    background-color: $line-orange-color;
}

.form-label-film {
  position: relative;
  display: flex;
  align-items: center;
    border-top: none;
    border-left: none;
    border-right: none;
  border-bottom: 0.5px solid $white-color;

  width: 280px;
    height: 20px;

    @media screen and (min-width: 768px) {
width: 336px;
}
}

.icon-search {
  position: absolute;
  top: 25%;
  right: 4px;
}

.input-film {
    padding: 4px 20px 4px 4px;
    color: $white-color;
    width: 100%;
    background-color: transparent;
    border: none;
}

.input-film::placeholder {
    padding: 4px;
    display: flex;
align-items: center;
    color: $white-color;
font-weight: 400;
font-size: 14px;
line-height: 1.14;
}

.search-form {
    display: flex;
    justify-content: center;
}

.search-form.is-hidden {
   display: none;
}

.btns-library.is-hidden {
  display: none;
}

.btns-library {
   .button-list {
    display: flex;
    align-items: center;
    justify-content: center;
    @extend %list;
   }

   .button-list-item:not(:nth-child(1)) {
    margin-left: 20px;

    @media screen and (min-width: 768px) {
    margin-left: 15px;
}
   }

    .button {
    @extend %button;
    background-color: transparent;
    border-color: $white-color;
    box-shadow: none;
    // border: 1px solid #FFFFFF;

    @include transit(background-color, border-color, box-shadow);

  &:hover,
  &:focus {
    background-color: $btn-accent-color;
    // border: 1px solid #FF6B01;
    border-color: $btn-accent-color;
    box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
  }
}
}

   




